<template>
   <div class="mergely-full-screen-8">
     <div class="mergely-resizer">
       <div @click="saveDiff">下载对比文件</div>
       <div id="mergely"></div>
     </div>
   </div>
</template>
 
<script>
import 'codemirror/lib/codemirror.js'
import 'codemirror/lib/codemirror.css'
import 'mergely/lib/mergely.css'
import 'mergely/lib/mergely.js'
 
export default {
  data() {
    return {
    }
  },
  mounted() {
    // 渲染diff界面
    
    const mergely = new Mergely(`#mergely`, {
      cmsettings:{mode: 'text/plain', readOnly: true},
    	sidebar: false,
      autoupdate:true,
      license:'lgpl-separate-notice',
    	lhs: (setValue) => {
    	  setValue('111')
    	},
    	rhs: (setValue) => {
    	  setValue('222')
    	},
    });
  },
  methods: {
    // 实现diff文件差异内容下载
    saveDiff() {
      let a = $('#mergely');
      var n = a.mergely('diff') // 获得对比差异数据
      let key = '文件名'
      // 创建a标签，下载diff文件
      let aHref = document.createElement('a');
      aHref.setAttribute('href','data:application/stream;base64,' + window.btoa(unescape(encodeURIComponent(n))));
      aHref.setAttribute('target','_blank');
      aHref.setAttribute('text','clickme');
      aHref.setAttribute('id',key);
      aHref.setAttribute('download', key + '.diff');
      document.body.appendChild(aHref);
      aHref.click();
      aHref.remove();
    }
    }
}
</script>

<style scoped>
  #mergely {
    height: 1000px;
  }
</style>